
html, body {
  height: 100%;
}
#home-container {
  height: 100%;
  overflow: hidden;
  background: #fbfbfb;
}
#home-container .header img {
    height: 46px;
    width: 46px;
}
#home-container .header .search-btn {
    height: 46px;
    width: 27px;
    background-image: url("../assets/search.png");
    background-position: center center;
    background-size: 27px 27px;
    background-repeat: no-repeat;
}
#home-container main {
    height: 100%;
}
#home-container main .loadmore-wrapper {
      height: 100%;
      padding-top: 46px;
      padding-bottom: 50px;
      overflow: scroll;
      -webkit-overflow-scrolling: auto;
}
#home-container main .loadmore ul {
      list-style: none;
}
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__label span {
  color: rgba(51, 0, 0, 0.7);
}
.vux-x-icon {
  fill: #999999;
}
.weui-bar__item_on .vux-x-icon {
  fill: rgba(0, 0, 0, 0.7);
}
.list-enter-active, .list-leave-active {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
.list-leave-active {
  opacity: 0;
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
}
.list-enter {
  opacity: 0;
  -webkit-transform: translateY(-30px);
          transform: translateY(-30px);
}



